import share from '@/components/share';
var token=""
var goodsId=""
export default{
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			goodsData:{},
			favorite: true,
			shareList: [],
			userInfo:{}
			
		};
	},
	onLoad(options){
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		this.userInfo = uni.getStorageSync(this.$userKey)
		token = uni.getStorageSync("token")
		goodsId = options.id; 
		this.loadGoods(goodsId)
	},
	
	methods:{
		loadGoods(id){
			var _this = this
			console.log("goodsId")
			console.log(goodsId)
			uni.request({
			    url:this.$url+'/goods/detail/'+id ,//服务器端地址
			    data: {
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.goodsData = res.data.data
			        }
			    },
				fail(res) {
					console.log(res)
				}
			   
			});
		},
		//购买
		buy(){
		if(!this.userInfo.nickname){
			url = '/pages/login/login';
		}else
		{
		var goodDatas=[]
		goodDatas.push(this.goodsData)
		uni.navigateTo({
			url: `/pages/order/createOrder?orders=`+encodeURIComponent(JSON.stringify(goodDatas))
		})	
		}
			
		},
		//添加购物车
		car(){
			if(!this.userInfo.nickname){
				url = '/pages/login/login';
			}else
			{
			var _this = this
			uni.request({
			    url:this.$url+'/car/add' ,//服务器端地址
			    data: {
					goodsId:goodsId
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						this.$api.msg('已添加购物车');
			        }
			    }
			   
			});
			}
		},
	
	},

}
